  <div class="popup popup1 task-popup" id="handle_order" v-show="showStatus">
    <div class="pop-content">
      <p class="popup-title">
        处理工单
      </p>
      <div class="popup-con">
        <ul>
          <li>
            <span>设备MAC</span>
            <p>{{task.mac}}</p>
          </li>
          <li>
            <span>告警时间</span>
            <p>{{task.alarmTime}}</p>
          </li>
          <li>
            <span>派工管理员</span>
            <p>{{task.adminUser}}</p>
          </li>
          <li>
            <span>派工时间</span>
            <p>{{task.createTime}}</p>
          </li>
          <!-- <li :class="{'warning': $v.task.handleResult.$dirty && $v.task.handleResult.$invalid}"> -->
          <li>
            <span class="name1" >处理结果</span>
            <!-- <textarea v-model="task.handleResult" @input="$v.task.handleResult.$touch"></textarea> -->
            <textarea v-model="task.handleResult"></textarea>
            <!-- <small v-show="$v.task.handleResult.$dirty && $v.task.handleResult.$invalid">*请输入处理结果</small> -->
          </li>
          <li>
            <span class="name1" >上传图片</span>
            <p>
              <a class="upload">
                <img src="../../../../assets/img/upload.png" />
                <input type="file" accept="image/png,image/jpg,image/jpeg" id="logo_file_i" @change="changeFile('logo_file_i', 'logo_pre')" multiple/>
              </a>
              <label>
                *最多上传三张图片
              </label>
            </P>
          </li>
          <li>
            <span class="name1" >图片预览</span>
            <p>
              <a class="upload">
                <img id="logo_pre" :src="preLogo" v-if="preLogo"/>
              </a>
              <a class="upload">
                <img id="logo_pre1" :src="preLogo1" v-if="preLogo1"/>
              </a>
              <a class="upload">
                <img id="logo_pre2" :src="preLogo2" v-if="preLogo2"/>
              </a>
            </P>
          </li>
          <li>
            <button class="btn btn-middle cancel" @click="cancel">取消</button>
            <button class="btn btn-middle yes" @click="submit">确定</button>
          </li>
        </ul>
      </div>
      <!--关闭按钮-->
      <span class="layui-layer-setwin">
				<a class="close-popup layui-layer-close layui-layer-close1" href="javascript:;" @click="cancel">
          <span class="iconfont iconguanbi close-iconfont"></span>
				</a>
			</span>
    </div>
  </div>
</template>
<script>
import { required } from 'vuelidate/lib/validators'
// import taskApi from '../../../server_api/task'
import msgUtil from '@/utils/msgUtil'
import { handleTask } from '@/api/monitor'

export default {
  data() {
    return {
      showStatus: false,
      preLogo: '',
      preLogo1: '',
      preLogo2: '',
      task: {
        id: '',
        handleResult: '',
        mac: '',
        alarmTime: '',
        user: '',
        createTime: ''
      }
    }
  },
  validations: {
    task: {
      handleResult: {
        required
      }
    }
  },
  methods: {
    show(info) {
      // 恢复验证
      // this.$v.task.handleResult.$reset()
      this.task = {
        id: info.id,
        phone: info.phone,
        receiver: info.handleUser,
        mac: info.mac,
        createTime: this.timestampToTime(info.createTime),
        alarmTime: this.timestampToTime(info.alarmTime),
        // createTime:info.createTime,
        // alarmTime:info.alarmTime,
        address: info.address,
        adminUser: info.adminUser
      }
      this.showStatus = true
      this.$el.style.zIndex = Date.now() % 100000000
    },
    timestampToTime(timestamp) {
      if (typeof (timestamp) == 'number') {
        var date = new Date(timestamp * 1000);// 时间戳为10位需*1000，时间戳为13位的话不需乘1000
        var Y = date.getFullYear() + '-';
        var M = (date.getMonth() + 1 < 10 ? '0' + (date.getMonth() + 1) : date.getMonth() + 1) + '-';
        var D = date.getDate() + ' ';
        var h = date.getHours() + ':';
        var m = date.getMinutes() + ':';
        var s = date.getSeconds();
        return Y + M + D + h + m + s;
      } else if (typeof (timestamp) == 'string') {
        return timestamp
      }
    },
    async submit() {
      // 触发验证
      // this.$v.task.handleResult.$touch()
      const file0 = document.getElementById('logo_file_i').files
      const formData = new FormData()
      for (var i = 0; i < file0.length; i++) {
        formData.append('upfile', file0[i])
      }
      formData.append('status', 4)
      formData.append('id', this.task.id)
      formData.append('memo', this.task.handleResult)
      try {

        const data = await handleTask(formData)
        msgUtil.success('处理工单成功')
        if (this.$route.path === '/index/monitor/monitor-center') {
          this.$store.dispatch('handleTask', this.task.id + '')
        }
        this.$emit('handle-success')
        console.log(data)
      } catch (error) {
        msgUtil.error('处理工单失败', msgUtil.findErrorMsg(error))
      }
      // const data = handleTask(
      //   formData
      // ).then(success => {
      //   this.showStatus = false
      //   msgUtil.success('处理工单成功')
      //   if (this.$route.path === '/index/monitor/monitor-center') {
      //     this.$store.dispatch('handleTask', this.task.id + '')
      //   }
      //   this.$emit('handle-success')
      // }, error => {
      //   msgUtil.error('处理工单失败', msgUtil.findErrorMsg(error))
      // })

    },
    changeFile(cur) {

      const file = document.getElementById(cur).files[0]
      if (file) {
        var fileReader = new FileReader()
        fileReader.onload = (e) => {
          if (cur === 'logo_file_i') {
            this.preLogo = e.target.result
          } else {
            this.preNotice = e.target.result
          }
        }
        fileReader.readAsDataURL(file)

      }

      const file1 = document.getElementById(cur).files[1]
      if (file1) {
        var fileReader1 = new FileReader()
        fileReader1.onload = (e) => {
          if (cur === 'logo_file_i') {
            this.preLogo1 = e.target.result
          } else {
            this.preNotice = e.target.result
          }
        }
        fileReader1.readAsDataURL(file1)
      }

      const file2 = document.getElementById(cur).files[2]
      if (file2) {
        var fileReader2 = new FileReader()
        fileReader2.onload = (e) => {
          if (cur === 'logo_file_i') {
            this.preLogo2 = e.target.result
          } else {
            this.preNotice = e.target.result
          }
        }
        fileReader2.readAsDataURL(file2)
      }
    },
    cancel() {
      this.showStatus = false
      this.preLogo = ''
      this.preLogo1 = ''
      this.preLogo2 = ''
    }
  }
}
</script>
